import React from 'react';
import {
  ArrowTrendingUpIcon,
  ArrowUturnRightIcon,
  BellAlertIcon,
  BuildingOffice2Icon,
  ClipboardDocumentListIcon,
  GlobeAltIcon,
  PuzzlePieceIcon,
  UserCircleIcon,
} from '@heroicons/react/24/outline';
import { useState, useRef, useEffect, useContext } from 'react';
import { useLocation } from "@reach/router";
import { navigate, Link } from 'gatsby';
import { StaticImage } from 'gatsby-plugin-image';

import { SearchContext } from '../context/SearchContext';
// import logo from '../assets/images/logo.png';

const Navbar = () => {
  const [showSidebar, setShowSidebar] = useState(false);
  const [changeColor, setChangeColor] = useState(false);
  const sidebarRef = useRef<any>(null);

  const location = useLocation();
  const { setSearchValue, searchValue } = useContext(SearchContext);

  const handleSearchChange = (value: string) => {
    setSearchValue(value);
    navigate(`/search-results?search-query=${value}`);
  };

  const toggleSidebar = () => {
    setShowSidebar(!showSidebar);
  };

  const handleClickOutside = (event: any) => {
    if (sidebarRef.current && !sidebarRef.current?.contains(event.target)) {
      setShowSidebar(false);
    }
  };

  const clearSearch = () => {
    setSearchValue('');
    if (location.pathname.includes('/search-results')) {
      navigate(-1);
    } else {
      history.back();
    }
  };

  useEffect(() => {
    document.addEventListener('mousedown', handleClickOutside);
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, []);

  const isSearch = () => new URLSearchParams(location.search).get('search-query');

  useEffect(() => {
    const queryParams = new URLSearchParams(location.search);
    const searchQuery = queryParams.get('search-query') || '';
    setSearchValue(searchQuery);
    if (location.pathname !== '/' || searchQuery) {
      setChangeColor(true);
    } else {
      setChangeColor(false);
    }
  }, [location.search]);

  return (
    <nav
      className={`w-full shadow-sm flex items-center md:justify-between px-4 py-2 md:py-4 md:px-8 ${
        changeColor && 'bg-[#3341BD] text-white'
      }`}
    >
      <div
        className={`flex items-center  ${isSearch() && 'md:hidden'}`}
      >
        <Link
          to='/'
          className='mr-2 text-secondarydark'
        >
          {/* <img src={logo} className='w-24' /> */}
          <StaticImage
            src="../assets/images/logo.png" // Replace with your image path
            alt="Description of the image"
            placeholder="blurred"
            layout="constrained"
            className='w-24'
          />
        </Link>
        <div className='border-r-2 h-6 bg-red-400 mr-2' />
        <div className='hidden md:flex space-x-4 font-bold'>
          <p
            onClick={() => handleSearchChange('Layer 1')}
            className={`${changeColor ? 'hover:text-red-200' : 'hover:text-[#3341BD] cursor-pointer'} cursor-pointer`}
          >
            Layer 1
          </p>
          <p
            onClick={() => handleSearchChange('RWA')}
            className={`${changeColor ? 'hover:text-red-200' : 'hover:text-[#3341BD] cursor-pointer'} cursor-pointer`}
          >
            RWA
          </p>
          <p
            onClick={() => handleSearchChange('Social')}
            className={`${changeColor ? 'hover:text-red-200' : 'hover:text-[#3341BD] cursor-pointer'} cursor-pointer`}
          >
            Social
          </p>
          <p
            onClick={() => handleSearchChange('Recent projects')}
            className={`${changeColor ? 'hover:text-red-200' : 'hover:text-[#3341BD] cursor-pointer'} cursor-pointer`}
          >
            Recently Added
          </p>
        </div>
      </div>

      <div
        className={`md:flex md:items-center md:justify-end space-x-2 ${
          isSearch() ? 'md:w-full' : 'w-1/2'
        }`}
      >
        <div className='relative w-full flex'>
          <input
            placeholder='Search here'
            value={searchValue}
            onChange={(e) => handleSearchChange(e.target.value)}
            className={`border-[1px] p-1 text-sm md:p-2 md:w-full w-56 text-black rounded-md bg-gray-100 outline-none ${
              isSearch() && 'md:rounded-full md:p-3'
            }`}
          />
          {isSearch() && (
            <p
              onClick={clearSearch}
              className='text-nowrap absolute right-6 underline text-secondarydark cursor-pointer text-xs top-[30%]'
            >
              Clear search
            </p>
          )}
        </div>
        <p className='bg-secondarylight p-2 rounded-md hidden md:inline'>
          <BellAlertIcon className='h-6 text-black bell-animation' />
        </p>
        <div className='md:flex space-x-2 hidden font-bold'>
          <p className='w-24 text-center m-auto'>About Us</p>
          <a
            onClick={clearSearch}
            href='https://www.discord.gg/dMUA4WRqVF'
            target='_blank'
            className='bg-secondarylight text-black p-2 rounded-md w-24 text-center'
          >
            Discord
          </a>
        </div>
      </div>

      {/* Menu icon */}
      <div className='md:hidden flex space-x-2 ml-auto cursor-pointer'>
        <UserCircleIcon className='h-6' />
        <svg
          className='h-6 w-6 text-secondarylight'
          xmlns='http://www.w3.org/2000/svg'
          fill='none'
          viewBox='0 0 24 24'
          stroke='currentColor'
          onClick={toggleSidebar}
        >
          <path
            strokeLinecap='round'
            strokeLinejoin='round'
            strokeWidth='2'
            d='M4 6h16M4 12h16m-7 6h7'
          />
        </svg>
      </div>

      {/* Sidebar */}
      {showSidebar && (
        <div
          ref={sidebarRef}
          className='md:hidden absolute inset-y-0 left-0 z-50 bg-secondarydark text-white w-64'
        >
          <p
            onClick={() => handleSearchChange('Layer 1')}
            className='px-4 py-4 hover:bg-primarydark w-full flex'
          >
            <p className='my-auto mx-2'>
              <ArrowTrendingUpIcon className='h-4' />
            </p>
            Layer 1
          </p>
          <p
            onClick={() => handleSearchChange('RWA')}
            className='px-4 py-4 hover:bg-primarydark w-full flex'
          >
            <p className='my-auto mx-2'>
              <PuzzlePieceIcon className='h-4' />
            </p>
            RWA
          </p>
          <p
            onClick={() => handleSearchChange('Social')}
            className='px-4 py-4 hover:bg-primarydark w-full flex'
          >
            <p className='my-auto mx-2'>
              <BuildingOffice2Icon className='h-4' />
            </p>
            Social
          </p>
          <p
            onClick={() => handleSearchChange('Recent projects')}
            className='px-4 py-4 hover:bg-primarydark w-full flex'
          >
            <p className='my-auto mx-2'>
              <ClipboardDocumentListIcon className='h-4' />
            </p>
            Recently Added
          </p>
          <Link
            to='/about'
            className='px-4 py-4 hover:bg-primarydark w-full flex'
          >
            <p className='my-auto mx-2'>
              <GlobeAltIcon className='h-4' />
            </p>
            About Us
          </Link>
          <Link
            to='/notifications'
            className='px-4 py-4 hover:bg-primarydark w-full flex'
          >
            <p className='my-auto mx-2'>
              <BellAlertIcon className='h-4' />
            </p>
            Notifications
          </Link>
          <a
            onClick={clearSearch}
            href='https://www.discord.gg/dMUA4WRqVF'
            className='px-4 py-4 hover:bg-primarydark w-full flex'
            target='_blank'
          >
            <p className='my-auto mx-2'>
              <ArrowUturnRightIcon className='h-4' />
            </p>
            Discord
          </a>
        </div>
      )}
    </nav>
  );
};

export default Navbar;
